html {
  font-family: 'Open Sans', sans-serif;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

html {
  font-size: .625em; }
  @media (max-width: 1023px) {
    html {
      font-size: .6em; } }
  @media (max-width: 767px) {
    html {
      font-size: .5em; } }

* {
  margin: 0;
  padding: 0;
  position: relative; }

*, *:before, *:after {
  -webkit-box-sizing: inherit;
  -moz-box-sizing: inherit;
  box-sizing: inherit; }

h1, h2, h3, h4, h5 {
  font-family: 'Roboto Slab', serif; }

h1 {
  font-weight: 700; }

h2 {
  font-weight: 400; }

p {
  font-weight: 300;
  line-height: 1.8em; }

a {
  text-decoration: none;
  color: inherit; }

.half {
  width: 50%;
  padding-right: 2rem; }
  @media (max-width: 767px) {
    .half {
      width: 100%;
      padding: 0 1rem; } }

.left {
  float: left; }

.right {
  float: right; }

@media (max-width: 767px) {
  .left,
  .right {
    float: none; } }

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0; }

.clearfix {
  display: inline-block; }

* html .clearfix {
  height: 1%; }

.clearfix {
  display: block; }

.wrap--outer {
  width: 1024px;
  margin: 0 auto; }
  @media (max-width: 1023px) {
    .wrap--outer {
      width: 100%; } }

.wrap--inner {
  width: 90%;
  margin: 0 auto;
  padding: 0 .3em;
  border-left: 1px dashed #e2e2e2;
  border-right: 1px dashed #e2e2e2; }

.sprite {
  display: block; }

.section--title {
  color: #48910f;
  font-size: 2.2rem;
  line-height: 4rem;
  padding: 1.4em 0 .6em; }

#intro {
  height: 80vh;
  color: #e2e2e2; }
  #intro .wrap--outer {
    background-color: green;
    background-image: url("../images/hi_imGG_bg--green.jpg");
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    background-position: center center;
    background-attachment: fixed;
    height: 100%; }
  #intro .wrap--inner {
    height: 100%; }
  #intro .container {
    height: 200px;
    top: 0;
    bottom: 0;
    margin: auto;
    position: absolute; }
  #intro h1 {
    font-size: 6em; }
  #intro p {
    font-size: 2rem; }
  #intro .svg-goDown {
    margin: 2em 0 1em; }
    @media (max-width: 767px) {
      #intro .svg-goDown {
        margin-left: auto;
        margin-right: auto; } }

#journey .wrap--inner {
  border-color: #48910f; }

#journey .journey__container {
  background-color: #f2f2f2;
  font-size: 1.4rem; }

#journey .journey__single .wrap--inner {
  padding-bottom: 3em; }

#journey .journey__single .left {
  width: 80%; }
  @media (max-width: 767px) {
    #journey .journey__single .left {
      width: 100%; } }

@media (max-width: 767px) {
  #journey .sprite__logo {
    margin: 0em 0 1em; } }

#journey .journey__year .wrap--inner {
  padding: 2em 0 1em; }

#journey .journey__year p {
  color: #e2e2e2;
  display: inline-block;
  text-align: center;
  font-weight: 600;
  font-size: .8rem;
  padding: .2em 0;
  left: -42px; }
  @media (max-width: 767px) {
    #journey .journey__year p {
      right: 0;
      left: auto;
      position: absolute; } }

#journey .journey__meta {
  text-transform: uppercase;
  font-weight: 100;
  font-size: .8em;
  line-height: 1em; }
  #journey .journey__meta span, #journey .journey__meta strong {
    margin-right: 1em; }
